import React, { Component } from 'react';
import { Consumer } from '../../context';
import TextInputGroup from '../layout/TextInputGroup';
import TextInputGroupSmall from '../layout/TextInputGroup';
//import uuid from 'uuid';
import axios from 'axios';
import styled from 'styled-components';

class AddContact extends Component {
  state = {
    name: '',
    email: '',
    phone: '',
    address: '',
    address2: '',
    city: '',
    state: '',
    postcode: '',
    country: '',
    errors: {}
  };

  onSubmit = async (dispatch, e) => {
    e.preventDefault();

    const {
      name,
      email,
      phone,
      address,
      city,
      state,
      postcode,
      country
    } = this.state;
    const numbers = /^[0-9]+$/;
    const letters = /^[a-zA-Z]+$/;

    //Check for Errors.
    if (name === '') {
      this.setState({ errors: { name: 'Name is required.' } });
      return;
    }

    if (email === '') {
      this.setState({ errors: { email: 'Email is required.' } });
      return;
    }

    if (phone === '') {
      this.setState({ errors: { phone: 'Phone is required.' } });
      return;
    } else if (!(phone.length === 10)) {
      this.setState({
        errors: {
          phone: "Not the right length! Make sure you don't have any dashes!"
        }
      });
      return;
    } else if (!phone.match(numbers)) {
      this.setState({
        errors: { phone: 'Did you really just try to input letters?' }
      });
      return;
    }

    if (address === '') {
      this.setState({ errors: { address: 'Address is required.' } });
      return;
    }

    if (city === '') {
      this.setState({ errors: { city: 'City is required.' } });
      return;
    } else if (!city.match(letters)) {
      this.setState({
        errors: {
          city: 'What kind of city has numbers in its name?'
        }
      });
      return;
    }

    if (state === '') {
      this.setState({ errors: { state: 'State is required.' } });
      return;
    } else if (!state.match(letters)) {
      this.setState({
        errors: {
          state:
            "States don't contain numbers in their names either. Come on man."
        }
      });
      return;
    }

    if (postcode === '') {
      this.setState({ errors: { postcode: 'Postal Code is required.' } });
      return;
    } else if (!postcode.match(numbers)) {
      this.setState({
        errors: {
          postcode:
            'Did...did you just input letters? Do you even know what a Post Code is?'
        }
      });
      return;
    } else if (!(postcode.length === 5)) {
      this.setState({
        errors: {
          postcode: 'ZIP Codes are 5 numbers long, dipshit.'
        }
      });
      return;
    }

    if (country === '') {
      this.setState({ errors: { country: 'Country is required.' } });
      return;
    } else if (!country.match(letters)) {
      this.setState({
        errors: {
          country:
            "This isn't planet Cirklon7B. Do I need to call Area 51 on your alien ass?"
        }
      });
      return;
    }

    const newContact = {
      name,
      email,
      phone
    };

    const res = await axios.post(
      `https://jsonplaceholder.typicode.com/users`,
      newContact
    );

    dispatch({ type: 'ADD_CONTACT', payload: res.data });

    //Clears the state.
    this.setState({
      name: '',
      email: '',
      phone: '',
      errors: {}
    });

    this.props.history.push('/');
  };

  onChange = e => this.setState({ [e.target.name]: e.target.value });

  render() {
    const {
      name,
      email,
      phone,
      address,
      address2,
      city,
      state,
      postcode,
      country,
      errors
    } = this.state;

    const FormDeets = styled.h1`
      font-size: 2em !important;
      font-family: 'Times New Roman', Times, serif;
      text-align: center;
    `;

    const Header = styled(FormDeets)`
      font-size: 3em !important;
      font-style: bold;
      color: white !important;
      background-color: #ff3860 !important;
      font-variant: small-caps;
    `;

    const SlickForm = styled.div`
      padding: 25px !important;
      background-color: #f2f2f2;
    `;

    return (
      <Consumer>
        {value => {
          const { dispatch } = value;
          return (
            <div className="box container" style={{ padding: '0px' }}>
              <Header className="column panel-heading">Add Contact</Header>
              <SlickForm className="column">
                <form onSubmit={this.onSubmit.bind(this, dispatch)}>
                  <TextInputGroup
                    label="Name"
                    name="name"
                    placeholder="Enter Name"
                    value={name}
                    onChange={this.onChange}
                    error={errors.name}
                  />
                  <TextInputGroup
                    label="Email"
                    name="email"
                    type="email"
                    placeholder="Enter Email"
                    value={email}
                    onChange={this.onChange}
                    error={errors.email}
                  />
                  <TextInputGroup
                    label="Phone"
                    name="phone"
                    placeholder="Enter Phone"
                    value={phone}
                    onChange={this.onChange}
                    error={errors.phone}
                  />
                  <FormDeets>Address Details</FormDeets>
                  <TextInputGroup
                    label="Line 1"
                    name="address"
                    placeholder="Address Line 1"
                    value={address}
                    onChange={this.onChange}
                    error={errors.address}
                  />
                  <TextInputGroup
                    label="Line 2"
                    name="address2"
                    placeholder="Address Line 2"
                    value={address2}
                    onChange={this.onChange}
                    error={errors.address2}
                  />

                  <div className="column-no-padding">
                    <div className="columns">
                      <div className="column is-one-third">
                        <TextInputGroup
                          label="City"
                          name="city"
                          placeholder="Enter City"
                          value={city}
                          onChange={this.onChange}
                          error={errors.city}
                        />
                      </div>

                      <div className="column is-one-third">
                        <TextInputGroupSmall
                          label="State"
                          name="state"
                          placeholder="Enter State"
                          value={state}
                          onChange={this.onChange}
                          error={errors.state}
                        />
                      </div>
                      <div className="column is-one-third">
                        <TextInputGroupSmall
                          label="Postal Code"
                          name="postcode"
                          placeholder="Enter Postal Code"
                          value={postcode}
                          onChange={this.onChange}
                          error={errors.postcode}
                        />
                      </div>
                    </div>
                  </div>

                  <div style={{ paddingTop: '15px' }} />
                  <TextInputGroup
                    label="Country"
                    name="country"
                    placeholder="Enter Country"
                    value={country}
                    onChange={this.onChange}
                    error={errors.country}
                  />

                  <div className="column is-10  is-offset-1">
                    <input
                      type="submit"
                      value="Add Contact"
                      className="button is-fullwidth"
                    />
                  </div>
                </form>
              </SlickForm>
            </div>
          );
        }}
      </Consumer>
    );
  }
}

export default AddContact;



