import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import { Consumer } from '../../context';
import axios from 'axios';

class Contact extends Component {
  state = {
    showContactInfo: false
  };

  onDeleteClick = async (id, dispatch) => {
    try {
      await axios.delete(`https://jsonplaceholder.typicode.com/users/${id}`);

      dispatch({ type: 'DELETE_CONTACT', payload: id });
    } catch (e) {
      dispatch({ type: 'DELETE_CONTACT', payload: id });
    }
  };

  render() {
    const { id, name, email, phone } = this.props.contact;
    const { showContactInfo } = this.state;
    const padding = {
      padding: '.75em'
    };
    const addMargin = {
      marginBottom: '1.5em'
    };
    return (
      <Consumer>
        {value => {
          const { dispatch } = value;
          return (
            <div className="card" style={addMargin}>
              <div className="column">
                <h4>
                  <header className="card-header">
                    <span className="column is-narrow">
                      <p className="card-header-title is-size-4 column is-narrow">
                        {name}
                      </p>
                    </span>
                    <i
                      onClick={() =>
                        this.setState({
                          showContactInfo: !this.state.showContactInfo
                        })
                      }
                      className="card-header-icon fas fa-sort-down"
                      style={{ cursor: 'pointer', float: 'left' }}
                    />
                    <div className="column">
                      <i
                        className="card-header-icon fas fa-times is-size-4"
                        style={{
                          cursor: 'pointer',
                          float: 'right',
                          color: 'red',
                          lineHeight: '1.5'
                        }}
                        onClick={this.onDeleteClick.bind(this, id, dispatch)}
                      />
                      <Link to={`contact/edit/${id}`}>
                        <i
                          className="card-header-icon fas fa-pencil-alt is-size-4"
                          style={{
                            cursor: 'pointer',
                            float: 'right',
                            color: 'black',
                            marginRight: '1rem',
                            lineHeight: '1.5'
                          }}
                        />
                      </Link>
                    </div>
                  </header>
                </h4>
              </div>
              {showContactInfo ? (
                <ul className="card-content" style={padding}>
                  <li className="panel-block"> Email: {email}</li>
                  <li className="panel-block">Phone: {phone}</li>
                </ul>
              ) : null}
            </div>
          );
        }}
      </Consumer>
    );
  }
}

Contact.propTypes = {
  contact: PropTypes.object.isRequired
};

export default Contact;



