import React from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import styled from 'styled-components';

const Header = props => {
  const { branding } = props;
  const PopNavItem = styled.i`
    color: white;
    opacity: 0.7;
    font-family: Arista;

    &:hover {
      opacity: 0.9;
    }
  `;
  return (
    <div className="column-no-padding has-background-danger">
      <div className="container">
        <div
          className="navbar 
        has-background-danger"
        >
          <a
            href="/"
            className="navbar-brand navbar-item has-text-white is-size-4 has-text-weight-bold"
          >
            {branding}
          </a>
          <div className="navbar-end">
            <ul className="navbar has-background-danger">
              <li className="navbar-item">
                <Link to="/">
                  <PopNavItem>
                    <i className="fas fa-home">Home</i>
                  </PopNavItem>
                </Link>
              </li>
              <li className="navbar-item">
                <Link to="/contact/add" className="">
                  <PopNavItem>
                    <i className="fas fa-plus">Add</i>
                  </PopNavItem>
                </Link>
              </li>
              <li className="navbar-item">
                <Link to="/about" className="">
                  <PopNavItem>
                    <i className="fas fa-question has-text-white">About</i>
                  </PopNavItem>
                </Link>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  );
};

Header.defaultProps = {
  branding: 'My App'
};

Header.propTypes = {
  branding: PropTypes.string.isRequired
};

export default Header;



